<template>
  <q-input
    dense
    outlined
    :type="inputType"
    v-model="passwordModel"
    :label="label"
    :placeholder="
      label ? '' : inputType === 'password' ? '********' : '--------'
    "
  >
    <template v-slot:prepend v-if="withicon">
      <q-icon size="xs" name="icon-lock-04" />
    </template>
    <template v-slot:append>
      <q-btn
        v-if="passwordModel"
        flat
        dense
        round
        size="md"
        :icon="inputType == 'password' ? 'icon-eye' : 'icon-eye-off'"
        class="bg-none text-grey-6"
        :ripple="false"
        @click="tooglePassword"
      />
    </template>
  </q-input>
</template>

<script>
import { defineComponent } from 'vue';

export default defineComponent({
  name: 'InputPassword',

  props: {
    label: {
      type: String,
    },
    withicon: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {
      passwordModel: '',
      inputType: 'password',
    };
  },

  methods: {
    tooglePassword(forceValue = null) {
      if (this.inputType === undefined) return;
      if (forceValue !== null && typeof forceValue === 'boolean')
        this.inputType = forceValue;
      else {
        this.inputType =
          this.inputType?.toLowerCase() === 'password' ? 'text' : 'password';
      }
    },
  },
});
</script>
