<template>
  <q-card flat class="bg-transparent">
    <q-carousel
      infinite
      animated
      swipeable
      v-model="activeTestimonial"
      control-color="secondary"
      ref="testimonialCarousel"
      transition-next="slide-left"
      transition-prev="slide-right"
      class="bg-transparent rounded fit"
    >
      <q-carousel-slide
        v-for="item in testimonials.slice(0, max)"
        :key="item.id"
        :name="item.id"
        class="q-px-none q-pt-none q-pb-xl fit"
      >
        <div class="full-width">
          <q-rating
            readonly
            v-model="item.rating"
            size="sm"
            class="color-gmc-green"
            icon="icon-star-fill"
          />

          <div class="q-py-lg font-medium text-h5">
            {{ item.comment }}
          </div>
          <q-img
            :src="item.avatar"
            :ratio="1"
            style="height: 58px; max-width: 58px"
            class="round"
            loading="lazy"
            spinner-color="secondary"
          />
          <div class="q-pt-md">
            <div class="font-medium">
              {{ item.firtname }} {{ item.lastname }}
            </div>
            <div class="text-grey-6">{{ item.label }}</div>
          </div>
        </div>
      </q-carousel-slide>

      <template v-slot:control>
        <q-carousel-control position="bottom-left" :offset="[0, 0]">
          <div class="q-col-gutter-sm q-pt-md">
            <span v-for="item in testimonials.slice(0, max)" :key="item.id">
              <q-icon
                name="icon-circle-01"
                size="14px"
                :color="
                  activeTestimonial === item.id
                    ? 'secondary'
                    : `grey-${dark ? '9' : '4'}`
                "
                class="hover-text-secondary cursor-pointer"
                @click="$refs.testimonialCarousel.goTo(item.id)"
              />
            </span>
          </div>
        </q-carousel-control>
      </template>
    </q-carousel>
  </q-card>
</template>

<script>
import { defineComponent } from 'vue';
import { mapState } from 'pinia';
import useGrowmodo from 'stores/growmodo';

export default defineComponent({
  name: 'GrowmodoTestimonials',

  props: {
    dark: {
      type: Boolean,
      default: false,
    },
    max: {
      type: Number,
      default: 5,
    },
  },

  data() {
    return {
      activeTestimonial: 1,
    };
  },

  computed: {
    ...mapState(useGrowmodo, ['testimonials']),
  },
});
</script>
