<template>
  <div class="row items-center q-gutter-x-sm">
    <q-linear-progress
      rounded
      size="24px"
      :value="subscriptionPercentage"
      track-color="grey-4"
      class="col cursor-pointer"
      color="secondary"
      style="border-radius: 24px"
    >
      <div
        class="absolute-full flex flex-center items-center"
        :style="`max-width: ${subscriptionPercentage * 100}%`"
      >
        <span
          class="text-white text-body2 text-center"
          :class="{ 'q-px-sm': $q.screen.gt.sm }"
          style="text-overflow: ellipsis; overflow: hidden; max-height: 20px"
          >{{ remainingDays }} {{ remainingDays > 1 ? 'Days' : 'Day' }}</span
        >
      </div>
    </q-linear-progress>
    <div class="col-auto">
      {{ subscriptionDaysLength }}
      {{ subscriptionDaysLength > 1 ? 'Days' : 'Day' }}
    </div>
  </div>
</template>
<script>
import { defineComponent } from 'vue';
import { mapActions, mapState } from 'pinia';
import useUser from 'stores/user';

export default defineComponent({
  name: 'RemainingDays-Progress',

  props: {
    contdown: {
      type: Boolean,
      default: false,
    },
  },

  computed: {
    ...mapState(useUser, ['subscriptionDaysLength']),

    subscriptionPercentage() {
      if (
        isNaN(this.remainingDays) ||
        this.remainingDays < 0 ||
        isNaN(this.subscriptionDaysLength)
      )
        return 0;
      return this.remainingDays / this.subscriptionDaysLength;
    },

    remainingDays() {
      let remainingDays = this.subscriptionRemaining(
        this.$root?.realtimeDate,
        'days'
      );
      if (!this.contdown)
        remainingDays = this.subscriptionDaysLength - remainingDays;
      return remainingDays || 0;
    },
  },

  methods: {
    ...mapActions(useUser, ['subscriptionRemaining']),
  },
});
</script>
