import { api, cdn } from 'src/boot/apis';
import useHelpers from 'stores/helpers';
import fnStore from '../fn.store';
const helpers = useHelpers();

// Start Subscriptionss
async function getSubscription_Talents() {
  const url = '/miscs/subscriptions/talents';
  return await api
    .get(url)
    .then((res) => {
      const data = res.data;
      if (data.data) {
        data.data.forEach((e) => {
          this.subscription_Talents[e.id] = e;
        });
      }
      fnStore.addAPICache(url, data);
      return data;
    })
    .catch((e) => {
      const cache_data = fnStore.getAPICache(url);
      if (cache_data.data) {
        cache_data.data.forEach((e) => {
          this.subscription_Talents[e.id] = e;
        });
      }
      return cache_data || e;
    });
}

