async function getPlatforms() {
  const url = '/miscs/platforms';
  return await api
    .get(url, {
      params: {
        per_page: -1,
      },
    })
    .then((res) => {
      const data = res.data;
      if (data.data?.data && data.data?.data?.length) {
        data.data.data.forEach((e) => {
          this.platforms[e.id] = e;
        });
      }
      fnStore.addAPICache(url, data);
      return data;
    })
    .catch((e) => {
      const cache_data = fnStore.getAPICache(url) || e;
      if (cache_data.data?.data && cache_data.data?.data?.length) {
        cache_data.data.data.forEach((e) => {
          this.platforms[e.title] = e;
        });
      }
      return cache_data || e;
    });
}

function getPlatformInfo(platform, key = null) {
  return platform
    ? this.platforms[platform] && key
      ? this.platforms?.[platform]?.[key]
      : this.platforms?.[platform]
    : 0;
}

function formatPlatformIconSrc(platform) {
  return `img:${cdn.baseURL}${cdn.platforms}/${
    this.getPlatformInfo(platform, 'icon') || platform
  }.png`;
}

function planPriceCalculator(talentCnt, billingId) {
  if (!talentCnt || !billingId) return {};
  const { formatMoney } = require('src/assets/scripts/functions');
  const billing = this.subscription_BillingTypes[billingId] || {};

  // Total Price
  let total =
    talentCnt === -1 || talentCnt === 6
      ? 0
      : this.subscription.base_price * talentCnt * billing.value;
  // Discount
  let discount =
    (billing.savings_percentage ? total * billing.savings : billing.savings) ||
    0;
  // Discount Rounded by 50
  let discount_rounded = Math.round(discount / 50) * 50;
  // Total Discount check if savings_per_month
  let discount_total =
    discount_rounded * (billing.savings_per_month ? billing.value : 1);
  // Total Discounte Price
  let total_discounted = total - discount_total;
  // Discount Label
  let discount_label =
    total === 0
      ? 'Custom'
      : helpers.getCurrency(billing.savings_currency)?.symbol +
        formatMoney(discount_total);
  // Final Label with Total Discounted Price
  let label =
    total === 0
      ? 'Custom'
      : helpers.getCurrency(this.subscription.savings_currency)?.symbol +
        formatMoney(total_discounted);

  return {
    label,
    total,
    total_discounted,
    discount,
    discount_rounded,
    discount_label,
    discount_total,
  };
}

// Task Categories
async function getTaskCategories() {
  const url = '/miscs/task-dirs/categories';
  return await api
    .get(url, {
      params: {
        per_page: -1,
      },
    })
    .then((res) => {
      const data = res.data || {};
      const res_data = data.data || {};
      if (res_data?.data && res_data.data?.length) {
        res_data.data.forEach((e) => {
          this.taskDirectory_Categories[e.tag] = e;
        });
      }
      fnStore.addAPICache(url, data);
      return data;
    })
    .catch((e) => {
      const cache_data = fnStore.getAPICache(url);
      const cache_res_data = data.data || {};
      if (cache_res_data?.data && cache_data.data?.length) {
        cache_res_data.data.forEach((e) => {
          this.taskDirectory_Categories[e.tag] = e;
        });
      }
      return cache_data || e;
    });
}

