<template>
  <q-list class="row q-col-gutter-md" v-if="scope">
    <template v-for="(file, i) in scope.files" :key="file.__key">
      <div
        :class="`${
          small || scope.files?.length === 1 ? 'col-12' : 'col-12 col-sm-6'
        }`"
      >
        <q-item
          dense
          class="border-add rounded q-py-sm hover-up-1 hover-border-secondary"
        >
          <q-item-section
            thumbnail
            v-if="(thumbnail && file.__img) || typeof thumbnail === 'string'"
            class="q-pl-md self-start"
          >
            <div
              class="overflow-hidden flex flex-center round bg-accent"
              style="width: 30px; height: 30px"
            >
              <img
                v-if="typeof thumbnail === 'boolean'"
                :ratio="1"
                :src="file.__img.src"
                style="width: 30px; height: unset"
              />
              <q-icon v-else :name="thumbnail" size="20px" color="secondary" />
            </div>
          </q-item-section>

          <q-item-section class="text-left">
            <q-item-label class="full-width ellipsis">
              <div class="text-body2">{{ file.name }}</div>
              <div class="text-grey-6 text-body2">
                {{ file.__sizeLabel }}
              </div>
            </q-item-label>

            <div class="row items-center q-col-gutter-sm">
              <div class="col-10">
                <q-linear-progress
                  indeterminate
                  size="sm"
                  :value="file.__progress"
                  color="secondary"
                />
              </div>
              <div class="col-2 text-right">
                {{ file.__progressLabel }}
              </div>
            </div>
          </q-item-section>

          <q-item-section class="absolute-top-right q-ma-sm">
            <q-btn
              flat
              dense
              round
              size="small"
              icon="icon-trash-01"
              class="hover-text-negative color-primary2"
              @click="scope?.removeFile?.(file) || scope?.removeFileById?.(i)"
            />
          </q-item-section>
        </q-item>
      </div>
    </template>
  </q-list>
</template>

<script>
import { defineComponent } from 'vue';

export default defineComponent({
  name: 'UploadSlot',

  props: {
    small: {
      type: Boolean,
      default: false,
    },
    scope: {
      type: Object,
      default: null,
    },
    thumbnail: {
      type: [Boolean, String],
      default: false,
    },
  },
});
</script>
