<template>
  <q-uploader
    flat
    multiple
    ref="modelRef"
    max-file-size="5000000"
    class="full-width files-uploader rounded full-height"
    @start="startEvent"
    @finish="finishEvent"
    @added="startEvent"
    @removed="finishEvent"
  >
    <template v-slot:header="scope">
      <div
        class="q-py-lg text-body2 color-primary2 text-center rounded cursor-pointer full-height flex items-center rounded border-add border-dashed hover-border-secondary"
        @click="$refs?.['uploadBtnTrigger']?.click?.()"
      >
        <div class="full-width">
          <div class="full-width">
            <q-btn
              flat
              :ripple="false"
              color="primary"
              class="bg-none"
              ref="uploadBtnTrigger"
              @click="scope.pickFiles"
            >
              <CardIconBox icon="icon-upload-01" />
              <q-uploader-add-trigger />
            </q-btn>
          </div>
          <div class="q-pt-md full-width">
            <span class="text-secondary font-medium">Click to upload</span> or
            drag and drop
          </div>
          <div class="full-width">{{ note }}</div>
        </div>
      </div>
    </template>

    <template v-slot:list="scope">
      <UploadSlot :thumbnail="thumbnail" :scope="scope" />
      <div
        v-if="scope?.files?.length > 4"
        class="absolute-bottom bg-gradient no-pointer-events sticky"
        style="height: 50px"
      ></div>
    </template>
  </q-uploader>
</template>

<script>
import { defineComponent } from 'vue';
import CardIconBox from './CardIconBox.vue';
import UploadSlot from './Slots/UploadSlot.vue';

export default defineComponent({
  name: 'FileUploader',

  components: { CardIconBox, UploadSlot },

  props: {
    note: {
      type: String,
      default: 'SVG, PNG, JPEG or GIF (max. 5MB)',
    },
    thumbnail: {
      type: [Boolean, String],
      default: false,
    },
    removedFile: {},
  },

  watch: {
    removedFile: {
      async handler(val) {
        if (val) this.$refs['modelRef']?.removeFile?.(val);
      },
    },
  },

  methods: {
    startEvent(e) {
      console.log('startEvent', e);
    },
    finishEvent(e) {
      console.log('finishEvent', e);
    },
  },
});
</script>
