<script>
import { defineComponent } from 'vue';
import {
  ColorThief,
  getImageFromBase64str,
  getImageStringFromURL,
  updateStepperHeader,
} from 'src/assets/scripts/functions';
import SelectCompanyType from 'src/components/Helpers/SelectCompanyType.vue';
import InputWebsite from 'src/components/Helpers/InputWebsite.vue';
import FileUploader from 'src/components/Helpers/FilesUploader.vue';
import { FileUploader_Mixin } from 'src/mixins';
import ColorPickerCard from 'src/components/Helpers/ColorPickerCard.vue';
import { colors, uid } from 'quasar';
import UploadSlot from 'src/components/Helpers/Slots/UploadSlot.vue';
import { mapActions } from 'pinia';
import useUser from 'stores/user';

export default defineComponent({
  name: 'CreateNewBrand',

  mixins: [
    // Uploader functions
    FileUploader_Mixin,
  ],

  components: {
    SelectCompanyType,
    InputWebsite,
    FileUploader,
    ColorPickerCard,
    UploadSlot,
  },

  emits: ['added', 'error', 'close'],

  props: {
    noclose: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {
      formStep: 1,
      formStepBrandInfo: 1,
      stepperHeaderElements: null,
      brandColorSrc: '',
      brandColorSrcBlob: '',
      validImageForColorPalette: false,
      brand: { colors: {}, customColors: [], social: {} },
      colorPalette: undefined,
      acceptedFontTypes: ['.woff2', '.woff', '.otf', '.ttf'],
      brandColors: {
        primary: {},
        secondary: {},
        accent: {},
        text: {},
      },
    };
  },

  watch: {
    formStep: {
      async handler(val) {
        this.stepperHeaderStyle();
        if (val > 1) this.rightDrawer = false;
        else this.rightDrawer = true;
      },
    },
    brandColorSrc: {
      async handler(val) {
        if (!val) {
          this.brand.colors = {};
          this.validImageForColorPalette = false;
        }
      },
    },
    brandColorSrcBlob: {
      async handler(val) {
        if (val) {
          let encodedImage = await getImageStringFromURL(
            val,
            this.acceptedImageTypes
          );
          if (encodedImage) {
            this.brandColorSrc = encodedImage;
            this.validImageForColorPalette = true;
            setTimeout(this.getColorPalette, 500);
          } else {
            this.brand.colors = {};
            this.validImageForColorPalette = false;
          }
        } else {
          this.brand.colors = {};
          this.validImageForColorPalette = false;
        }
      },
    },
  },

  mounted() {
    this.stepperHeaderElements =
      this.$refs?.['formStepper']?.$el?.firstChild?.childNodes;
    this.stepperHeaderStyle();
  },

  methods: {
    ...mapActions(useUser, ['addOrgBrand']),

    async getColorPalette() {
      let retry = 0;
      let result = this.getColorPaletteFn();
      while (!result && retry <= 5) {
        result = this.getColorPaletteFn();
        retry++;
      }
    },

    async getColorPaletteFn() {
      try {
        var imageColorThief = new ColorThief();
        let sourceImage = await getImageFromBase64str(this.brandColorSrc);
        this.brand.colors = {};
        if (sourceImage) {
          let colorPalettes = imageColorThief.getPalette(sourceImage, 100);
          if (!colorPalettes) return;
          this.colorPalette = colorPalettes.map((e) => `rgb(${e.join(',')})`);
          colors.textToRgb(`rgb(${colorPalettes[3].join(',')})`);
          // Set Brand Colors
          let color_p = colorPalettes?.[0];
          this.brand.colors.primary =
            colors.rgbToHex({
              r: color_p[0],
              g: color_p[1],
              b: color_p[2],
            }) || '#000000';
          let color_s = colorPalettes?.[1];
          this.brand.colors.secondary =
            colors.rgbToHex({
              r: color_s[0],
              g: color_s[1],
              b: color_s[2],
            }) || '#000000';
          let color_a = colorPalettes?.[2];
          this.brand.colors.accent =
            colors.rgbToHex({
              r: color_a[0],
              g: color_a[1],
              b: color_a[2],
            }) || '#000000';
          let color_t = colorPalettes?.[3];
          this.brand.colors.text =
            colors.rgbToHex({
              r: color_t[0],
              g: color_t[1],
              b: color_t[2],
            }) || '#000000';
        }
        return true;
      } catch (e) {
        return false;
      }
    },

    onRejected(rejectedEntries) {
      let message = `File${
        rejectedEntries?.length > 1 ? 's' : ''
      } did not pass validation constraints!`;
      this.$q.notify({
        icon: 'icon-alert-triangle',
        iconColor: 'negative',
        message,
      });
    },

    addedImgColorSource(files) {
      files.forEach((file, i) => {
        if (i === files.length - 1) {
          this.$q.notify({
            spinner: true,
            message: `Getting colors from ${file.name}.`,
            timeout: 500,
          });
          this.brandColorSrcBlob = file?.__img?.src;
        }
      });
    },

    stepperHeaderStyle() {
      if (this.stepperHeaderElements) {
        updateStepperHeader(this.stepperHeaderElements, this.formStep);
      } else {
        this.stepperHeaderElements =
          this.$refs?.['formStepper']?.$el?.firstChild?.childNodes;
      }
    },

    prevButtonClicked() {
      this.$refs?.['formStepper']?.previous?.();
    },

    nextButtonClicked() {
      if (this.brand.category && this.brand.label) {
        this.$refs?.['formStepper']?.next?.();
      } else {
        this.$q.notify({
          icon: 'icon-alert-triangle',
          iconColor: 'negative',
          message: 'Brand information is required',
        });
        this.$emit('error');
      }
    },

    brandsPrevButtonClicked() {
      this.$refs?.['brandInformationStepper']?.previous?.();
    },

    brandsNextButtonClicked() {
      this.$refs?.['brandInformationStepper']?.next?.();
    },

    submitFormAction() {
      if (this.brand.category && this.brand.label) {
        const newUID = uid();
        this.brand.id = newUID;
        this.brand.gid = newUID;
        this.addOrgBrand(newUID, this.brand);

        this.$q.notify({
          icon: 'icon-check-circle-broken',
          message: 'Your brand is successfully saved',
        });
        this.$emit('added', newUID);
        this.$emit('close');
      } else {
        this.$q.notify({
          icon: 'icon-alert-triangle',
          iconColor: 'negative',
          message: 'Brand information is missing',
        });
        this.$emit('error');
      }
    },

    customColorSlideLeft(slideOpt, customColorIndex) {
      this.brand.customColors.splice(customColorIndex, 1);
    },
  },

  beforeUnmount() {
    this.$emit('close');
  },
});
</script>
