import { axios } from 'src/boot/apis';

function getCustomField({ name, gid, key, caseSensetive = true }) {
  let cf = this.custom_fields.length
    ? this.custom_fields.filter((cf) =>
        caseSensetive
          ? // Case Sensetive
            String(name ? cf.name : cf.gid).toLowerCase() ==
            String(name || gid).toLowerCase()
          : // None Case Sensetive
          name
          ? cf.name
          : cf.gid === name || gid
      )
    : [];
  return key ? cf?.[0]?.[key] : cf?.[0];
}

function getCustomFieldEnum({
  name,
  gid,
  caseSensetive = true,
  enum_name,
  enum_gid,
  enum_caseSensetive = true,
  key,
}) {
  let cf = this.getCustomField({
    name,
    gid,
    key: 'enum_options',
    caseSensetive,
  });
  if (cf && cf.length) {
    let enum_opt = cf.filter((e) =>
      enum_caseSensetive
        ? // Case Sensetive
          String(enum_name ? e.name : e.gid).toLowerCase() ==
          String(enum_name || enum_gid).toLowerCase()
        : // None Case Sensetive
        enum_name
        ? e.name
        : e.gid === enum_name || enum_gid
    );
    if (enum_opt && enum_opt.length)
      return key ? enum_opt?.[0]?.[key] : enum_opt?.[0];
  }
}

async function createTask(payload) {
  let response = { success: 0 };

  try {
    const payload_formated = {
      ...payload,
      pretty: process.env.DEV ? true : false,
    };
    return await axios
      .post(this.api?.ssrEndpoint, {
        endpoint: `${this.api.baseURL}/tasks`,
        body: JSON.stringify({
          data: payload_formated,
        }),
        method: 'post',
      })
      .then((result) => {
        if (result.gid) {
          response.success = 1;
          this.tasks[result.guid] = result;
        } // else Error
        return { ...response, response: result };
      })
      .catch((err) => {
        return { ...response, response: err };
      });
  } catch (err) {
    return { ...response, response: err };
  }
}

async function getCustomFieldsForWorkspace({ workspace, payload = {} }) {
  let response = { success: 0 };

  try {
    const payload_formated = {
      ...payload,
      pretty: process.env.DEV ? true : false,
    };
    let workspaceGid = workspace || this.workspace;
    return await axios
      .post(this.api?.ssrEndpoint, {
        endpoint: `${this.api.baseURL}/workspaces/${workspaceGid}/custom_fields`,
        body: JSON.stringify({
          data: payload_formated,
        }),
        method: 'get',
      })
      .then((result) => {
        if (result?.data) {
          response.success = 1;
          this.custom_fields = result.data;
        } // else Error
        return { ...response, response: result };
      })
      .catch((err) => {
        return { ...response, response: err };
      });
  } catch (err) {
    return { ...response, response: err };
  }
}

async function createEnumOptionForCustomField({
  customFieldGid,
  payload = {},
}) {
  let response = { success: 0 };

  try {
    const payload_formated = {
      ...payload,
      pretty: process.env.DEV ? true : false,
    };
    return await axios
      .post(this.api?.ssrEndpoint, {
        endpoint: `${this.api.baseURL}/${customFieldGid}/enum_options`,
        body: JSON.stringify({
          data: payload_formated,
        }),
        method: 'post',
      })
      .then((result) => {
        if (result?.gid) {
          response.success = 1;
          let indexOfCustomField = null;
          if (this.custom_fields.length)
            this.custom_fields.filter((e, i) => {
              if (e.gid === customFieldGid) {
                indexOfCustomField = i;
                return true;
              }
            });
          if (indexOfCustomField !== null)
            if (this.custom_fields.length)
              this.custom_fields?.[indexOfCustomField]?.enum_options.push(
                result
              );
        } // else Error
        return { ...response, response: result };
      })
      .catch((err) => {
        return { ...response, response: err };
      });
  } catch (err) {
    return { ...response, response: err };
  }
}

export {
  createTask,
  getCustomField,
  getCustomFieldEnum,
  getCustomFieldsForWorkspace,
  createEnumOptionForCustomField,
};
