function customFieldsGIDs(state) {
  return state.custom_fields.length
    ? state.custom_fields.map((c) => c.gid)
    : state.custom_fields;
}

function customFieldsNames(state) {
  return state.custom_fields.length
    ? state.custom_fields.map((c) => c.name)
    : state.custom_fields;
}

function allCustom_fields(state) {
  return {
    gids: customFieldsGIDs(state),
    names: customFieldsNames(state),
  };
}

export { customFieldsGIDs, customFieldsNames, allCustom_fields };
