<template>
  <q-btn
    no-caps
    unelevated
    :ripple="{ color: 'accent' }"
    color="primary"
    size="md"
    :class="{ rounded: !$q.screen.xs, 'not-gm': $q.screen.xs }"
    :round="$q.screen.xs"
  >
    <q-icon v-if="alwaysShowIcon || $q.screen.xs" :name="icon" size="sm" />
    <span
      v-if="$q.screen.gt.xs"
      :class="alwaysShowIcon || $q.screen.xs ? 'q-pl-xs' : ''"
      >{{ label }}</span
    >
    <tooltip-pro v-if="$q.screen.xs" :delay="150" :hide-delay="150">
      {{ label }}
    </tooltip-pro>
  </q-btn>
</template>

<script>
import { defineComponent } from 'vue';

export default defineComponent({
  name: 'DynamicButton',

  props: {
    label: {
      type: String,
      default: '',
    },
    icon: {
      type: String,
      default: '',
    },
    alwaysShowIcon: {
      type: Boolean,
      default: false,
    },
  },
});
</script>
