<template>
  <q-card flat class="row" :class="`q-col-gutter-${gridGutter}`">
    <div v-if="canAdd" :class="gridClass">
      <q-card
        flat
        bordered
        class="card-quicklinks card-quicklinks-new border-add border-dashed bg-color full-height row items-center justify-center hover-border-secondary"
        @click="addBrand = true"
      >
        <q-card-section class="text-center flex-center flex">
          <q-avatar
            rounded
            size="55px"
            class="rounded q-pa-sm text-white bg-primary auto-hw"
          >
            <q-icon
              name="icon-plus"
              color="white"
              size="sm"
              class="q-pa-xs"
              style="color: white !important"
            />
          </q-avatar>
          <div class="text-body1 font-medium q-pt-md full-width">
            Add New Brand
          </div>
        </q-card-section>
      </q-card>
    </div>

    <!-- Brands List -->
    <transition
      appear
      :duration="500"
      enter-active-class="animated fade"
      leave-active-class="animated fade"
      v-for="(brand, i) in brands"
      :key="i"
    >
      <div :class="gridClass">
        <q-card
          flat
          bordered
          v-ripple:accent
          class="card-quicklinks brand full-height slide-hover row items-center hover-border-secondary"
          @click="$router.push('/workspace/' + brand.id)"
        >
          <q-card-section class="self-start full-width q-pb-sm">
            <div
              v-if="brandCategories?.[brand?.category]?.label"
              class="full-width"
            >
              <div class="row item-center q-gutter-sm">
                <q-badge
                  rounded
                  class="badge font-medium"
                  :class="{
                    'badge-bg-disable':
                      !brandCategories?.[brand?.category]?.bg_color,
                    'badge-text-disable':
                      !brandCategories?.[brand?.category]?.color,
                  }"
                  :style="`background-color: ${
                    brandCategories?.[brand?.category]?.bg_color
                  }; color: ${brandCategories?.[brand?.category]?.color}`"
                >
                  {{ brandCategories?.[brand?.category]?.label }}
                </q-badge>
              </div>
            </div>
            <div class="text-h5 font-medium q-pt-md q-pb-sm brand-title">
              {{ limitBrandName(brand.brand_name) }}
            </div>
          </q-card-section>
          <q-card-actions
            align="right"
            class="full-width q-px-md bg-color self-end text-secondary text-body2 font-medium"
            :to="brand.id"
            :href="brand.id"
          >
            View Brand Overview
            <q-icon
              size="sm"
              name="icon-arrow-narrow-right"
              class="slide-hover-left-md q-pl-sm"
            />
          </q-card-actions>
        </q-card>
      </div>
    </transition>

    <!-- Add New Brand -->
    <q-dialog
      persistent
      v-model="addBrand"
      transition-show="scale"
      transition-hide="scale"
      :transition-duration="300"
    >
      <CreateNewBrand
        style="max-width: 90vw; width: 640px"
        @added="addBrand = false"
        @close="addBrand = false"
      />
    </q-dialog>
  </q-card>
</template>

<script>
import { defineComponent } from 'vue';
import CreateNewBrand from 'src/components/Brands/CreateNewBrand.vue';
import { mapState } from 'pinia';
import useUser from 'stores/user';
import useGrowmodo from 'stores/growmodo';

export default defineComponent({
  name: 'BrandsList',

  components: { CreateNewBrand },

  props: {
    canAdd: {
      type: Boolean,
      default: true,
    },
    maxBrand: {
      type: Number,
      default: 0,
    },
    reverseList: {
      type: Boolean,
      default: true,
    },
    gridClass: {
      type: String,
      default: 'col-12 col-sm-6 col-md-3',
    },
    gridGutter: {
      type: String,
      default: 'md',
    },
  },

  data() {
    return {
      maxTitle: 33,
      addBrand: false,
    };
  },

  computed: {
    ...mapState(useUser, ['selectedOrgBrands']),
    ...mapState(useGrowmodo, ['brandCategories']),

    brands() {
      let visibleBrands = Object.values(this.selectedOrgBrands);
      // Check if Need to Reverse
      if (this.reverseList) visibleBrands = visibleBrands.reverse();
      // Slice to Maximimun Brands
      if (this.maxBrand && typeof this.maxBrand === 'number')
        visibleBrands = visibleBrands.slice(0, this.maxBrand);
      return visibleBrands;
    },
  },

  methods: {
    limitBrandName(brandName) {
      let addDot = brandName?.length > this.maxTitle;
      return String(brandName).slice(0, 33) + (addDot ? '...' : '');
    },
  },
});
</script>
